<template>
  <div class="header">
    <div class="logo" :style="{ color: getColor }">
      {{ isLogin ? getColor : "#d98c07" }}
    </div>
    <button v-if="!isLogin" class="btn-login" @click="signIn">Sign In</button>
    <div v-else class="auth-box">
      <img
        class="avatar"
        :style="{ outline: '3px solid ' + getColor }"
        :src="user.photoURL"
        alt="avatar"
      />
      <button class="btn-exit" @click="logOut">Sign Out</button>
    </div>
  </div>
</template>

<script>
import { useAuth } from "@/firebase";
import { useColors } from "@/firebase";

export default {
  setup() {
    const { user, isLogin, signIn, logOut } = useAuth();
    const { colors } = useColors();

    return {
      user,
      isLogin,
      signIn,
      logOut,
      colors,
    };
  },
  computed: {
    getColor() {
      if (this.isLogin) {
        let color = "";
        this.colors.forEach((element) => {
          if (element.userId == this.user.uid) {
            color = element.color;
            return;
          }
        });
        return color;
      } else return "#d98c07";
    },
  },
};
</script>
